$FormInputBorderRadius: 8px;
$formtextalign: left;
$form-line-height: 2;
$FormBtnBorder: 1px solid #e26a2c;
$FormBtnColor: #e26a2c;


.shipping-information_section{
    font-family: $font-family
}

.guest-checkout_details{
    border: $FormBorder;
    padding: 10px 10px;
    font-size: $FormFontLabelSize;
}
.edit-label{
    display: none;
}
.payment-shipping_contact-details{
    line-height: 2;
}
@media only screen and (min-width: 768px) and (max-width: 1024px) {
    // .shipping-information_section{
    //     display: grid;
    //     grid-template-columns: 3fr 1fr;
    //     grid-gap: 1em;
    // }
    .edit-label{
        display:block;
        color: $form-btn-color;
    }
    .edit-details{
        width: 50px;
        display: flex;
        justify-content: space-between;
    }
    .payment-shipping_contact-details{
        line-height: 2;
        display: grid;
        grid-template-columns: 1fr 2fr;
    }
}
