$HeaderUnderlineColor: #e26a2c;
$CheckoutPageColor: 172026;
$FontFamily: 'Mulish', sans-serif;
$FormFontLabelSize: 14px;
$FormFontWeight: 600;
$FormBorder: 1px solid #B5B7BC;
$FormInputBorderRadius: 8px;
$FormTextAlign: left;
$FormLineHeight: 2;
$FormBtnBorder: 1px solid #e26a2c;
$FormBtnColor: #e26a2c;

.contact-information_header{
    background-color: $HeaderUnderlineColor;
    height: 5px;
    margin-left: 110px;
    margin-right: 110px;
}

@media only screen and (min-width: 768px) and (max-width: 1024px) {
    .contact-information_header {
        background-color: #e26a2c;
        height: 5px;
        margin-left: 414px;
        margin-right: 414px
    }
}
@media only screen and (min-width: 1025px) and (max-width: 1920px)  {
    .contact-information_header {
        background-color: #e26a2c;
        height: 5px;
        margin-left: 607px;
        margin-right: 607px;
    
    }
}