$font-size: 16px;
$formtextalign : left;

.shipping-method_section{
    font-family: $font-family;
    font-size: $font-size;
}

.payment-shipping_contact-details{
    line-height: 2;
}
.edit-label{
    display:block;
    color: $form-btn-color;
}
.edit-details{
    width: 50px;
    display: flex;
    justify-content: space-between;
}

@media only screen and (min-width: 768px) and (max-width: 1024px) {
    .payment-shipping_contact-details{
        line-height: 2;
        display: grid;
        grid-template-columns: 1fr 2fr;
        text-align: $formtextalign;
    } 
    .edit-label{
        display:block;
        color: $form-btn-color;
    }
    .edit-details{
        width: 50px;
        display: flex;
        justify-content: space-between;
    }
}

@media only screen and (min-width: 1025px) and (max-width: 1920px)  {
    .payment-shipping_contact-details{
        line-height: 2;
        display: grid;
        grid-template-columns: 1fr 2fr;
        text-align: $formtextalign;
    }
    .edit-label{
        display:block;
        color: $form-btn-color;
    }
    .edit-details{
        width: 50px;
        display: flex;
        justify-content: space-between;
    }
}
