$font-text-align : left;
$font-labels : 14px;
$form-lineheight: 2;
$form-border: 1px solid #b5b7bc;
$form-font-label-size: 14px;
$form-btn-border: 1px solid #e26a2c;
$form-btn-color: #e26a2c;
$header-underline-color: #e26a2c;
$font-family: 'Mulish', sans-serif;
$font-color: 172026;


.payment-selection-details_section{
    font-family: $font-family;
    color: $font-color;
}
.edit-label{
    display:block;
    color: $form-btn-color;
}
.edit-details{
    width: 50px;
    display: flex;
    justify-content: space-between;
}


@media only screen and (min-width: 768px) and (max-width: 1024px) {
    .edit-label{
        display:block;
        color: $form-btn-color;
    }
    .edit-details{
        width: 50px;
        display: flex;
        justify-content: space-between;
    }
}

@media only screen and (min-width: 1025px) and (max-width: 1920px)  {
    .edit-label{
        display:block;
        color: $form-btn-color;
    }
    .edit-details{
        width: 50px;
        display: flex;
        justify-content: space-between;
    }
}