@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@200;300&family=Playfair+Display:ital,wght@1,500&display=swap');


    $banner-font-family: 'Mulish', sans-serif;
    $banner-font-size: 16px;
    $banner-font-weight: 700;



.banner-container{
    display: grid;
    grid-template-columns: 1fr 2fr;
    margin-top: 30px;
    margin-bottom: 30px;
    padding: 0px 140px;
    font-family: $banner-font-family;

}
.banner-block{
    background-color: #333;
    color: #ffffff;
    padding: 105px 0px;
}
.banner-image{
    margin-top: 20px;
    margin-bottom: 15px;
}
.Banner-image{
    width: 100%;
}
   
@media only screen and (max-width: 767px) and (min-width: 320px) {
    .banner-container{
        display: block;
        grid-template-columns: 0;
        margin-top: -14px;
        padding: 0px 0px;
    }
    .banner-block{
        position: absolute;
        width: 94%;
        height: 150px;
        padding: 0px 0px;
        margin-left: 10px;
        margin-top: 85px;
    }
    .Banner-image{
        height: auto;
        width: 100%;
    }

}
