@import './../../variables.scss';

.order-summary_section{
    font-family: $font-family;
    color: $font-color;
    // padding: 0px 80px;
}
.order-summary_underline{
    width: 20px;
    height: 20px;
}
.order-summary_shipping-info{
    line-height: $form-lineheight;
    border: $form-border;
    text-align: $font-text-align;
    margin-top: 1em;
    padding: 10px 10px;
}
.order-summary_offer{
    display: block;
    background-color: $header-background-color;
    color: $color;
    line-height: $form-lineheight;
    padding: 20px 20px;
    height: 250px
}
.order-summary_social-icons{
    display: flex;
    margin-left: 176px;
    gap: 34px;
}
.social-icons {
    display: flex;
    width: 22px;
    height: 32px;
}
.order-number{
    text-align: $font-text-align ;
}
.order-summary_underline{
    background-color: $HeaderUnderlineColor;
    width: 150px;
    background-color: #e26a2c;
    height: 5px;
    margin-left: 94px;
    margin-right: 198px;
}
.order-number{
    padding: 0px 10px;
}

@media only screen and (min-width: 768px) and (max-width: 1024px) {
    .order-summary_section{
        font-family: $font-family;
        color: $font-color;
        padding: 0px 80px;
    }
    .order-summary_details{
        display: grid;
        grid-template-columns: 2.5fr 1fr;
        grid-gap: 1em;
    }
    .order-summary_grid{
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-gap: 1em;
    }
    .order-summary_offer{
        display: block;
        background-color: $header-background-color;
        color: $color;
        line-height: $form-lineheight;
        padding: 20px 20px;
        height: 250px;
        margin-top: 1em;
    }
    .order-summary_social-icons{
        display: flex;
        margin-left: 96px;
        gap: 34px;
    }
    .order-summary_underline{
        background-color: $HeaderUnderlineColor;
        width: 150px;
        background-color: #e26a2c;
        height: 5px;
        margin-left: 356px;
        margin-right: 408px;
    }
    .order-number{
        padding: 0px 10px;
    }
}

@media only screen and (min-width: 1025px) and (max-width: 1920px)  {
    .order-summary_section{
        font-family: $font-family;
        color: $font-color;
        padding: 0px 140px;
    }
    .order-summary_details{
        display: grid;
        grid-template-columns: 2.5fr 1fr;
        grid-gap: 1em;
    }
    .order-summary_grid{
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-gap: 1em;
    }
    .order-summary_offer{
        display: block;
        background-color: $header-background-color;
        color: $color;
        line-height: $form-lineheight;
        padding: 20px 20px;
        height: 250px;
        margin-top: 1em;
    }
    .order-summary_social-icons{
        display: flex;
        margin-left: 96px;
        gap: 34px;
    }
    .order-summary_underline{
        background-color: #e26a2c;
        height: 5px;
        margin-left: 532px;
        margin-right: 532px;
        width: 150px;
    
    }
}

